
import Header from '../../components/header/Header.vue'

import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import API from "../../api/api.js"

export default {
data() {
return {
name: "",
tel: "",
adr_detail: "",
is_default:0,
provice: [],
city:[],
area:[],
dialog_swt:false,
transitionName:'slide-up',
tr_one:"slide-left",
provice_id:"",
provice_name:"",
city_id:"",
city_name:"",
area_id:"",
area_name:"",
tab_id:1,
phoneReg:/(^1[3|4|5|7|8]\d{9}$)/,
}
},
components: {
'header-view': Header,
XSwitch,
Group,
Toast,
XButton
},
mounted() {
var _this = this;
if(this.dialog_swt){
this.transitionName="slide-down"
}else {
this.transitionName="slide-up"
}
this.get_city().then(res => {
_this.provice = res;
});
if(this.$route.query.id){
this.eidt_info(this.$route.query.id)
}
},
methods: {
get_city( t, p_id) {
return new Promise((resolve, reject) => {
this.$http.post(API.regions, {
type: t,
parent_id: p_id
}).then((res) => {
resolve(res.data.data)
}).catch((err) => {
reject(err)
})

}
)

},
tab(index){
this.tab_id=index
if(index==1){
this.tr_one="slide-right";
this.provice_id="";
this.provice_name="";
this.city_id="";
this.city_name="";
this.area_id="";
this.area_name="";
}else {
this.tr_one="slide-left"
}

if(index==2){
this.city_id="";
this.city_name="";
this.area_id="";
this.area_name="";
}
},
choose_provice(id,name){
var _this=this;
this.provice_id=id;
this.provice_name=name;
this.tab_id=2;
this.get_city(2,id).then(res=>{
_this.city=res;
})
},
choose_city(id,name){
var _this=this;
this.tab_id=3;
this.city_id=id;
this.city_name=name;
this.get_city(3,id).then(res=>{
_this.area=res;
})
},
choose_area(id,name){
this.area_id=id;
this.area_name=name;
},
set_def_adr(e){
e?this.is_default=1:this.is_default=0;
},
save(){
var _this=this;
if(this.name==""){
_this.$vux.toast.text("请输入姓名！", 'middle');
}else if(!_this.phoneReg.test(_this.tel)){
_this.$vux.toast.text("请输入正确的手机号！", 'middle');
}else if(this.provice_id=="" || this.city_id=="" ||this.area_id==""){
_this.$vux.toast.text("请将省市区填写完整！", 'middle');
}else if(this.adr_detail ==""){
_this.$vux.toast.text("请输入详细地址！", 'middle');
}else{
this.$http.post(API.add,{
consignee:this.name,
province:this.provice_id,
city:this.city_id,
district:this.area_id,
address:this.adr_detail,
tel:this.tel,
is_default:this.is_default
}).then(res=>{
_this.$vux.toast.text(res.data.msg, 'top');
_this.$router.back();
})
}
},
reset(){
this.name="";
this.tel="";
this.is_default=0;
this.adr_detail="";
this.area_id="";
this.area_name="";
this.city_id="";
this.city_name="";
this.provice_id="";
this.provice_name="";
this.tab_id=1;
},
hide_dialog(){
this.dialog_swt=!this.dialog_swt;
this.area_id="";
this.area_name="";
this.city_id="";
this.city_name="";
this.provice_id="";
this.provice_name="";
this.tab_id=1;
},
save_city(){
this.dialog_swt=!this.dialog_swt;
},

eidt_info(id){
var _this=this;

this.$http.get(API.adr_info,{
params:{
address_id:id
}
}).then(res=>{
var data=res.data.data;
_this.area_id=data.district;
_this.area_name=data.district_name;
_this.city_id=data.city;
_this.city_name=data.city_name;
_this.provice_id=data.province;
_this.provice_name=data.province_name;
_this.tel=data.tel;
_this.name=data.consignee;
_this.adr_detail=data.address;
_this.is_default=data.is_default;
})
},
save_edit(){
var _this=this;
if(this.name==""){
_this.$vux.toast.text("请输入姓名！", 'middle');
}else if(!_this.phoneReg.test(_this.tel)){
_this.$vux.toast.text("请输入正确的手机号！", 'middle');
}else if(this.provice_id=="" || this.city_id=="" ||this.area_id==""){
_this.$vux.toast.text("请将省市区填写完整！", 'middle');
}else if(this.adr_detail ==""){
_this.$vux.toast.text("请输入详细地址！", 'middle');
}else{
this.$http.put(API.adr_edit,{

address_id:this.$route.query.id,
consignee:this.name,
province:this.provice_id,
city:this.city_id,
district:this.area_id,
address:this.adr_detail,
tel:this.tel,
is_default:this.is_default
}).then(res=>{
_this.$vux.toast.text(res.data.msg, 'top');
_this.$router.back();
})
}
}

},



}



