import Header from "../../components/header/Header.vue";
import API from "../../api/api.js";
import { Dialog } from "vant";
export default {
  data() {
    return {
      withdrawLists: [],
      currentPage: 1,
      pagecount: 1
    };
  },
  components: {
    "header-view": Header
  },
  mounted() {
    this.getwithdrawLists();
  },
  methods: {
    getwithdrawLists() {
      var _this = this;
      _this.$http
        .get(API.withdraw, {
          params: {
            limit: 2,
            page: _this.currentPage
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            _this.withdrawLists = res.data.data.list;
            _this.pagecount = res.data.data.pagecount;
          }
        });
    },
    addcard() {
      this.$router.push({
        name: "cardadd",
        query: { choosecard: this.$route.query.choosecard ? 1 : 0 }
      });
    },
    cardInfo(id) {
      this.$router.push({
        name: "cardedit",
        query: { id: id, choosecard: this.$route.query.choosecard ? 1 : 0 }
      });
    }
  }
};



