
import XButton from 'vux/src/components/x-button/index.vue'


import Header from '../../components/header/Header.vue'
import API from "../../api/api.js"

export default {
data(){
return{
see_pwd_swt:false,
txt:"获取验证码",
pwd:"",
tel:"",
passwad:"",
passwad_two:"",
btn_swt:false
}
},
components:{
'header-view':Header,
XButton
},
methods:{
can_see_pwd(){
this.see_pwd_swt=!this.see_pwd_swt;
},
get_phonecode(ev){

var _this=this;
this.$http.post(API.send,{
mobile:this.tel,
type:3
}).then(res=>{
_this.$vux.toast.text(res.data.msg, 'top');
if(res.data.code==200){
_this.btn_swt=true;
var n=60;
var timer=setInterval(function() {
--n;
_this.txt=n+"s重新获取";
if(n<=0){
clearInterval(timer);
_this.btn_swt=false;
_this.txt="获取验证码";
}
},1000)
}
}).catch(err=>{

})
},
reset_pwd(){
var _this=this;
this.$http.post(API.forget,{
mobile:this.tel,
password:this.passwad,
repassword:this.passwad_two,
sms_code:this.pwd
}).then(res=>{
_this.$vux.toast.text(res.data.msg, 'top');
setTimeout(function(){
_this.$router.push({name:"login"});
},1500)
})
}

}
}



