
import Header from "../../components/header/Header.vue";
import Scroller from 'vux/src/components/scroller/index.vue'

import API from "../../api/api.js";

export default {
data() {
return {
title: "专题",
parent_list: [],
num: 0,

cat_id: 0,
beon: 0,
canbuy:true,
sort: 0,
order: 0,
page: 1,
limit: 10,
nowcat:'',
fliter_id: 1,
goods_data: [],
Scroller_hight: "calc(100% - 1.8rem)",
data_swt: true,
price_swt: false,
time_swt: false,
hs_swt: false,
nowcate:0,
};
},
components: {
"header-view": Header,
Scroller
},
mounted() {
var _this = this;
if (this.$route.query.cat_name) {
this.title = this.$route.query.cat_name;
}

if (this.$route.query.parent_id) {
this.Scroller_hight = "calc(100% - 2.6rem)";
this.fetch(this.$route.query.parent_id).then(res => {
this.parent_list = res;
this.num = res.length;
});
}
if (this.$route.query.cat_id) {
this.cat_id = this.$route.query.cat_id;
this.get_goods(
this.cat_id,
this.beon,
this.sort,
this.order,
this.page,
this.limit
).then(res => {
res.forEach(function(v,i){
if(i==0 && _this.goods_data==[]){
_this.nowcate = v.category;
}else if(v.category != _this.nowcate){
_this.nowcate = v.category;
v.isshow = true;
}else{
v.isshow = false;
}
_this.goods_data.push(v)
})
if (res.length < 10 || res == "") {
_this.data_swt = false;
} else {
_this.data_swt = true;
}

});
}
},
methods: {
fetch(parent_id) {
return new Promise((resolve, reject) => {
this.$http
.get(API.parent_list, {
params: {
parent_id: parent_id
}
})
.then(res => {
resolve(res.data.data);
})
.catch(res => {
reject(res);
});
});
},
get_goods(cat_id, beon, sort, order, page, limit) {
return new Promise((resolve, reject) => {
this.$http
.get(API.product_list, {
params: {
cat_id: cat_id,
beon: beon,
sort: sort,
order: order,
page: page,
limit: limit
}
})
.then(res => {
resolve(res.data.data);
})
.catch(res => {
reject(res);
});
});
},
blank(str, paixu, order) {
var _this = this;
this.goods_data = [];
this.$refs.scrollerEvent.reset({ top: 0 });
this.data_swt = true;
this.page = 1;
if (str == "price") {
this.sort = paixu;
this.order = order;
this.price_swt = false;
}
if (str == "time") {
this.beon = paixu;
this.order = order;
this.time_swt = false;
_this.canbuy = false;
}
this.get_goods(
this.cat_id,
this.beon,
this.sort,
this.order,
this.page,
this.limit
).then(res => {
res.forEach(function(v,i){
if(i==0 && _this.goods_data==[]){
_this.nowcate = v.category;
}else if(v.category != _this.nowcate){
_this.nowcate = v.category;
v.isshow = true;
}else{
v.isshow = false;
}
_this.goods_data.push(v)
})
if (res.length < 10 || res == "") {
this.data_swt = false;
}
});
},
change_fliter(fliter_id) {
this.fliter_id = fliter_id;
var _this = this;
switch (fliter_id) {
case 1:
this.price_swt = !this.price_swt;
this.time_swt = false;
break;
case 2:
this.time_swt = false;
this.price_swt = false;
this.sort = 1;
this.order = 0;
this.page=1;
this.goods_data = [];
this.$refs.scrollerEvent.reset({ top: 0 });

this.data_swt=true;
this.get_goods(
this.cat_id,
this.beon,
this.sort,
this.order,
this.page,
this.limit
).then(res => {res.forEach(function(v,i){
if(i==0 && _this.goods_data==[]){
_this.nowcate = v.category;
}else if(v.category != _this.nowcate){
_this.nowcate = v.category;
v.isshow = true;
}else{
v.isshow = false;
}
_this.goods_data.push(v)
})
if (res.length < 10 || res == "") {
this.data_swt = false;
}
});
break;
case 3:
this.hs_swt = !this.hs_swt;
this.time_swt = false;
this.price_swt = false;
break;
case 4:
this.time_swt = !this.time_swt;
this.price_swt = false;
break;
case 5://是否可订购
this.time_swt = false;
this.price_swt = false;
this.sort = 1;
this.order = 0;
this.page=1;
this.goods_data = [];
this.$refs.scrollerEvent.reset({ top: 0 });

this.data_swt=true;
this.canbuy = !this.canbuy;
if(this.canbuy){
this.beon = 0;
}else{
this.beon = 5;
}
this.get_goods(
this.cat_id,
this.beon,
this.sort,
this.order,
this.page,
this.limit
).then(res => {res.forEach(function(v,i){
if(i==0 && _this.goods_data==[]){
_this.nowcate = v.category;
}else if(v.category != _this.nowcate){
_this.nowcate = v.category;
v.isshow = true;
}else{
v.isshow = false;
}
_this.goods_data.push(v)
})
if (res.length < 10 || res == "") {
this.data_swt = false;
}
});
break;
}
},
change_cat_id(cat_id, cat_name) {
var _this = this;
if(cat_id!=this.cat_id){
this.cat_id = cat_id;
this.title = cat_name;

this.$refs.scrollerEvent.reset({ top: 0 });
this.goods_data = [];
this.page = 1;
this.data_swt = true;
this.get_goods(
cat_id,
this.beon,
this.sort,
this.order,
this.page,
this.limit
).then(res => {
res.forEach(function(v,i){
if(i==0 && _this.goods_data==[]){
_this.nowcate = v.category;
}else if(v.category != _this.nowcate){
_this.nowcate = v.category;
v.isshow = true;
}else{
v.isshow = false;
}
_this.goods_data.push(v)
})
if (res.length < 10 || res == "") {
this.data_swt = false;
}
});
}else{
return;
}
},

onScroll_buttom() {
var _this = this;
if (_this.data_swt) {
this.page = ++this.page;
this.data_swt = false;
this.get_goods(
this.cat_id,
this.beon,
this.sort,
this.order,
this.page,
this.limit
).then(res => {
if (res.length < 10 || res == "") {
this.data_swt = false;
} else {
this.data_swt = true;
}
res.forEach(function(v,i){
if(i==0 && _this.goods_data==[]){
_this.nowcate = v.category;
}else if(v.category != _this.nowcate){
_this.nowcate = v.category;
v.isshow = true;
}else{
v.isshow = false;
}
_this.goods_data.push(v);
})
});
}
}
}
};



