import Header from "../../../components/header/Header.vue";
import API from "../../../api/api.js";
import { Dialog } from "vant";
export default {
  data() {
    return {
      cardLists: []
    };
  },
  components: {
    "header-view": Header
  },
  mounted() {
    this.getcard();
  },
  methods: {
    choosecard(item) {
      if (this.$route.query.choosecard) {
        this.$router.push({
          name: "withdraw",
          query: {
            choose_card_data: JSON.stringify(item)
          }
        });
      }
    },
    carddel(id) {
      var _this = this;
      Dialog.confirm({
        title: "确认",
        message: "确认删除？"
      }).then(() => {
        _this.$http.delete(API.cardDelete + id).then(res => {
          var d = res.data;
          if (d.code == 200) {
            this.$vux.toast.text(d.msg, "middle");
            _this.getcard();
          }
        });
      });
    },
    getcard() {
      var _this = this;
      _this.$http
        .get(API.cardLists, {
          params: {
            key: localStorage.getItem("key"),
            limit: 100
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            _this.cardLists = res.data.data.list;
          }
        });
    },
    addcard() {
      this.$router.push({
        name: "cardadd",
        query: { choosecard: this.$route.query.choosecard ? 1 : 0 }
      });
    },
    cardInfo(id) {
      this.$router.push({
        name: "cardedit",
        query: { id: id, choosecard: this.$route.query.choosecard ? 1 : 0 }
      });
    }
  }
};



