
import foot from "../../components/footer/Footer";
import API from "../../api/api.js";
import utils from "../../utils/utils.js";
import v_scorll from "../../components/b_scorll/b_scorll";
import { Dialog } from "vant";

import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'


export default {
components: {
Swiper,
foot,
SwiperItem,
Scroller,
"v-scroll": v_scorll
},

data() {
return {
index_data: {},
toutiao: "",
jrdp: [],
theme: [],
page: 1,
limit: 2,
num: 0, //多个tab
data_swt: true
};
},

created() {

this.fetch(this.page, this.limit).then(res => {
this.index_data = res;
this.toutiao = res.toutiao;
this.jrdp = res.jrdp;
this.num = res.nav.length;
this.theme = res.theme;
});







var _this = this;
},

methods: {
fetch(page, limit) {
return new Promise((resolve, reject) => {
this.$http
.get(API.index, {
params: {
page: page,
limit: limit
}
})
.then(res => {
resolve(res.data.data);
})
.catch(res => {
reject(res);
});
});
},

tab(redirect_type, redirect_id) {
this.go_page(redirect_type, redirect_id);
},

go_apge(url, name) {
window.location.href = url;
},
go_page(redirect_type, redirect_id, name) {

switch (redirect_type) {

case 1:
this.$router.push({
name: "goodsDetail",
query: {
gshp_id: redirect_id
}
});
break;

case 2:
this.$router.push({
name: "category",
query: { cat_id: redirect_id }
});

break;

case 3:
this.go_apge(redirect_id, name);
break;
default:
break;
}
},
go_search() {
this.$router.push({ name: "search" });
},
onRefresh() {

},
onInfinite() {
if (this.data_swt) {
this.data_swt = false;
this.page = ++this.page;
this.fetch(this.page, 2).then(res => {
if (res.theme.length < 2) {
this.data_swt = false;
} else {
this.data_swt = true;
}

for (var i in res.theme) {





this.theme.push(res.theme[i]);
}

});
}
}
}
};



