import { setTimeout } from "timers";
export default {
  props: ["cartnumnew"],
  data() {
    return {
      cartnum: 0,
      footer: [
        {
          name: "首页",
          urlname: "../index",
          title: "/index",
          icon: "&#xe61f;",
          isactive: true,
          shownum: false
        },
        {
          name: "分类",
          urlname: "../sort",
          title: "/sort",
          icon: "&#xe602;",
          isactive: false,
          shownum: false
        },
        {
          name: "购物车",
          urlname: "../shopcart",
          title: "/shopcart",
          icon: "&#xe801;",
          isactive: false,
          shownum: true
        },
        {
          name: "会员中心",
          urlname: "../usercenter",
          title: "/usercenter",
          icon: "&#xe64e;",
          isactive: false,
          shownum: false
        }
      ]
    };
  },
  created() {
    // setTimeout(function(){
    // console.log('cartnumnew=='+this.cartnumnew)
    this.cartnum = localStorage.getItem("cartnum") || 0;
    // localStorage.setItem("cartnum",this.cartnum)
    // },1000)

    for (var i = 0; i < this.footer.length; i++) {
      if (this.footer[i].title == this.$route.path) {
        this.footer[i].isactive = true;
      } else {
        this.footer[i].isactive = false;
      }
    }
  },
  methods: {}
};



