import Header from "../../components/header/Header.vue";
import { Dialog } from 'vant';
import API from "../../api/api.js";

export default {
  data() {
    return {
      adr_list: [],
      start_Pos: {
        startX: 0,
        startY: 0
      },
      is_dingjin: 0
    };
  },
  components: {
    "header-view": Header
  },
  mounted() {
    this.fetch();
  },
  methods: {
    fetch() {
      var _this = this;
      this.$http
        .get(API.lists, {
          params: {}
        })
        .then(function(res) {
          var item = res.data.data;
          var isflag = false;
          item.forEach(function(v, i) {
            v.isTouchMove = false;
            if(v.is_default==1){
                isflag = true;
            }
          });
          if(!isflag){
            var data = {
              address_id:item[0].address_id,
              consignee:item[0].consignee,
              province:item[0].province,
              city:item[0].city,
              district:item[0].district,
              address:item[0].address,
              tel:item[0].tel,
              is_default:1
            }
            _this.edit(data);
            item[0].is_default = 1;
          }
          _this.adr_list = item;
        })
        .catch(function(res) {});
    },
    edit:function(data){
     this.$http.put(API.adr_edit,data).then(res=>{
      //  _this.$vux.toast.text(res.data.msg, 'top');
      //  _this.$router.push({name:'adr_list'})
     })
    },
    _touchStart: function(ev) {
      ev = ev || event;
      this.start_Pos.startX = ev.changedTouches[0].clientX;
      this.start_Pos.startY = ev.changedTouches[0].clientY;
      var item = this.adr_list;
      item.forEach(function(v, i) {
        if (v.isTouchMove)
          //只操作为true的
          v.isTouchMove = false;
      });
      this.adr_list = item;
    },
    _touchMove: function(ev, index) {
      ev = ev || event;
      var index = index,
        startX = this.start_Pos.startX,
        startY = this.start_Pos.startY,
        touchMoveX = ev.changedTouches[0].clientX,
        touchMoveY = ev.changedTouches[0].clientY,
        angle = this.angle(
          { X: startX, Y: startY },
          { X: touchMoveX, Y: touchMoveY }
        ),
        item = this.adr_list;
      item.forEach(function(v, i) {
        v.isTouchMove = false;
        //滑动超过30度角 return
        if (Math.abs(angle) > 20) return;
        if (v.address_id == index) {
          if (touchMoveX > startX)
            //右滑
            v.isTouchMove = false; //左滑
          else v.isTouchMove = true;
        }
      });
      this.adr_list = item;
    },
    _touchEnd: function(index) {},

    angle: function(start, end) {
      var _X = end.X - start.X,
        _Y = end.Y - start.Y;
      //返回角度 /Math.atan()返回数字的反正切值
      return 360 * Math.atan(_Y / _X) / (2 * Math.PI);
    },
    chosen_adr(index) {
      var _this = this;
      var d = _this.adr_list[index];
      // console.log(d)
      var address = {
        address_id:d.address_id,
        consignee:d.consignee,
        tel:d.tel,
        province_name:d.province_name,
        city_name:d.city_name,
        district_name:d.district_name,
        address:d.address
      }
        // console.log(address)
      if(_this.$route.query.sendaddress){
        this.$router.push({
          name: "editOrder",
          query: {
              choose_r_data:JSON.stringify(address)
          }
        });
      }
    },
    deleteItem: function(index, i) {
      var _this = this;
      Dialog.confirm({
        title: '确认',
        message: '确认删除地址吗？'
      }).then(() => {
      this.$http
        .delete(API.del_adr, {
          params: {
            address_id: index
          }
        })
        .then(res => {
          if (res.data.code == 200) {
            // _this.fetch();
            _this.adr_list.splice(i, 1);
          }
        });
      })
    },
    edit_adr(id) {
      this.$router.push({ name: "new_adr", query: { id: id } });
    }
  }
};



