

import foot from "../../components/footer/Footer";
import Scroller from 'vux/src/components/scroller/index.vue'

import API from "../../api/api.js";

export default {
data() {
return {
parent_list: [],
cat_id: "",
Bgoodimg: "",
cat_name: "",
right_data: []
};
},
components: {
Scroller,foot
},
created() {

this.fetch().then(res => {
this.parent_list = res;
this.cat_id = res[0].cat_id;
this.Bgoodimg = res[0].banner.img_url;
this.cat_name = res[0].cat_name;
this.fetch(this.cat_id).then(res => {
this.right_data = res;
});
});
},
methods: {
fetch(parent_id) {
return new Promise((resolve, reject) => {
this.$http
.get(API.parent_list, {
params: {
parent_id: parent_id
}
})
.then(res => {
resolve(res.data.data);
})
.catch(res => {
reject(res);
});
});
},
tab_sort(cat_id, Bgoodimg, cat_name) {
if (this.cat_id != cat_id) {
this.cat_id = cat_id;
this.Bgoodimg = Bgoodimg.img_url;
this.cat_name = cat_name;
this.right_data = [];
this.fetch(this.cat_id).then(res => {
this.right_data = res;
});
} else {
return;
}
},
go_category(cat_id, cat_name) {
this.$router.push({
name: "category",
query: { cat_id: cat_id, parent_id: this.cat_id, cat_name: cat_name }
});
}
}
};



