
import Header from "../../components/header/Header.vue";
import dialog_bottom from "../../components/dialog_bottom/dialog_bottom.vue";
import { Dialog } from "vant";
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import API from "../../api/api.js";
import { setTimeout } from "timers";
import $ from "jquery";

export default {
components: {
"header-view": Header,
"dialog-bottom": dialog_bottom,
XSwitch,
Group,
Radio
},
data() {
return {
data: {},
goodslist: [],
orderid: "",
message: "",
status: "",
showrefund: false,
refundgood: [],
pay_swt: false,
is_choose_card: null,
url: "",
configdata: []
};
},

mounted() {
var _this = this;
var id = _this.$route.params.id;
_this.orderid = id;
_this.url = window.location.href;

_this.getdetail(id);
},
methods: {
getdetail(id) {
var _this = this;
_this.$http
.get(API.orderdetail, {
params: { key: localStorage.getItem("key"), order_id: id }
})
.then(res => {
_this.data = res.data.data;
_this.message = _this.data.order_sn;
_this.goodslist = res.data.data.exist_real_goods;
var refundgood = [];

_this.goodslist.forEach(function(v, i) {
if (v.o_status == 4 || v.o_status == 2 || v.o_status == 1) {
refundgood.push({
name: v.goods_name,
id: v.goods_id,
isactive: false
});
}
});
_this.refundgood = refundgood;

var s = res.data.data.o_status;
if (s == 0 && _this.data.is_delete != 1) {
s = "待付款";
} else if (s == 0 && _this.data.is_delete == 1) {
s = "交易关闭";
} else if (s == 1 || s == 2) {
s = "待发货";
} else if (s == 4) {
s = "待收货";
} else if (s == 5 && _this.data.status == 0) {
s = "退货中";
} else if (s == 6 && _this.data.status != 0) {
s = "退货完成";
} else if (s == 7) {
s = "交易完成";
}
_this.status = s;

let info = JSON.parse(localStorage.getItem("info"));
});
},
gogoods(id) {
this.$router.push({ name: "goodsDetail", query: { gshp_id: id } });
},

onCopy: function() {
this.$vux.toast.text("复制成功", "middle");
},
onError: function() {
this.$vux.toast.text("无法复制", "middle");
},

hide_dialog_refund() {
this.showrefund = false;
},
checked_refund(i) {
var _this = this;
_this.refundgood[i].isactive = !_this.refundgood[i].isactive;
},

sureid() {
var _this = this;
var id = [];
_this.refundgood.forEach(function(v, i) {
if (v.isactive == true) {
id.push(v.id);
}
});
if (id.length == 0) {
_this.$vux.toast.text("你未选择需售后商品", "middle");
} else {
var refund_goods = [];
_this.goodslist.forEach(function(v, i) {
id.forEach(function(a, j) {
if (v.goods_id == a) {
refund_goods.push(v);
return false;
}
});
});
localStorage.setItem("refundgoods", JSON.stringify(refund_goods));

this.$router.push({
name: "afterservice",
params: {
orderid: _this.orderid
}
});
}
},

cancelorder(order) {
var _this = this;

Dialog.confirm({
title: "确认",
message: "确认取消订单？"
})
.then(() => {
_this.$http
.put(API.cancelorder, {
key: localStorage.getItem("key"),
order_id: order
})
.then(res => {
var d = res.data;
if (d.code == 200) {
this.$vux.toast.text(d.msg, "middle");
setTimeout(function() {
window.location.reload();
}, 1500);
}
});
})
.catch(() => {

});
},

sureorder(order) {
var _this = this;
Dialog.confirm({
title: "确认",
message: "确认收货？"
})
.then(() => {
_this.$http
.put(API.sure_receive, {
key: localStorage.getItem("key"),
order_id: order
})
.then(res => {
var d = res.data;
if (d.code == 200) {
this.$vux.toast.text(d.msg, "middle");
setTimeout(function() {
window.location.reload();
}, 1500);
}
});
})
.catch(() => {

});
},


cancelcancel(order) {
var _this = this;

Dialog.confirm({
title: "确认",
message: "确认取消退货？"
}).then(() => {
_this.$http
.put(API.cancelcancel, {
key: localStorage.getItem("key"),
order_id: order
})
.then(res => {
var d = res.data;
if (d.code == 200) {
this.$vux.toast.text(d.msg, "middle");
setTimeout(function() {
window.location.reload();
}, 1500);
}
});
});
},
hide_dialog_pay() {
this.pay_swt = !this.pay_swt;
},
choose_pay_way(i, FCardNumber) {
this.is_choose_card = i;
this.FCardNumber = FCardNumber;
}
}
};



