export default {
  props: {
    title: {
      type: String,

      default: ""
    },

    swt: {
      type: Boolean,

      default: false
    },
    urlname: {
      type: String,

      default: ""
    }
  },

  data() {
    return {
      is_Android: false
    };
  },

  methods: {
    close_win() {
      if (this.urlname) {
        this.$router.replace({
          name: this.urlname
        });
      } else {
        if (this.swt) {
          window.close();
        } else {
          // console.log(window.history.length)
          // console.log(this.$router.currentRoute)
          if (window.history.length <= 1 || !localStorage.getItem("key")) {
            this.$router.push({ name: "index" });
          } else {
            window.history.go(-1);
          }
        }
      }
    }
  },

  mounted() {
    // console.log("上一页："+document.referrer)
    // console.log(window.history.length)
    this.is_Android = navigator.userAgent.indexOf("Android") > -1;
  }
};



