
import Scroller from 'vux/src/components/scroller/index.vue'

import utils from "../../utils/utils.js";
import API from "../../api/api.js";
import vscorll from "../../components/b_scorll/b_scorll";
export default {
data() {
return {
isshow: false, //排序显示
isfilter: false, //筛选显示
glassshow: false, //蒙层显示
saleshow:true,//销量显示
pxactive:false,
zindex: 1,
keywords:'',
sort:'sales',
order:'DESC',
lowprice: "",
highprice: "",
page:2,
havedata:true,
isshowstyle:-1,
goodslist:[],
sortlist: [],
pxcont:'综合排序',
paixulist: [
{ name: "按上新排序", isactive: false ,title:'add_time'},
{ name: "价格由高到低", isactive: false ,title:'sort_price'},
{ name: "价格由低到高", isactive: false ,title:'sort_price',order:'ASC'}
]
};
},
components: { Scroller,vscorll },
methods: {
sou() {
var _this = this;
if (_this.keywords != "") {
this.getlist(1);
_this.hc_sou_history.unshift(_this.keywords);
} else {
_this.$vux.toast.text("输入不能为空", "middle");
}
var arr = utils.unique(_this.hc_sou_history);
_this.hc_sou_history = arr;
_this.save_storage("hc_sou_history", arr);
},

showstyle(){
this.isshowstyle = -this.isshowstyle;

},
pxlist(index,item,order,name) {
var that = this;
that.sort = item;
if(order !=undefined){
that.order = 'asc'
}
for (var i = 0; i < that.paixulist.length; i++) {
that.paixulist[i].isactive = false;
}
that.paixulist[index].isactive = true;
that.isshow = false;
that.glassshow = false;
that.saleshow = false;
that.pxactive = true;
that.pxcont = name;
that.goodslist = [];
that.getlist(1);
},

pxsale(){
var that = this;
that.sort = 'sales';
that.order = 'desc';
that.saleshow = true;
that.pxcont = '综合排序';
for (var i = 0; i < that.paixulist.length; i++) {
that.paixulist[i].isactive = false;
}
that.pxactive = false;
that.isshow = false;
that.glassshow = false;
that.goodslist = [];
that.getlist(1);

},
show_page() {
this.prompt();
},
prompt(keywords) {
var _this = this;
this.$http
.get(API.prompt, {
params: {
keywords: keywords
}
})
.then(res => {
_this.page_swt = true;
_this.prompt_data = res.data.data;
});
},

paixu() {
var _this = this;
_this.isshow = true;
_this.glassshow = true;
_this.zindex = 1;
},

noshow() {
var _this = this;
_this.isshow = false;
_this.glassshow = false;
_this.isfilter = false;
},
back() {
this.$router.back();
},

getsort() {
var _this = this;
_this.$http.get(API.parent_list, {}).then(res => {
res.data.data.unshift({ cat_name: "全部", cat_id: 0 });
var sortlist = res.data.data;
for(var i in sortlist){
sortlist[i].isactive=false;
}
_this.sortlist = sortlist;

});

},

clicksort(catid) {
var _this = this;
var sortData = _this.sortlist;
for (var i = 0; i < sortData.length; i++) {
if (sortData[i].cat_id == catid) {
sortData[i].isactive = true;
} else {
sortData[i].isactive = false;
}
}
_this.sortlist = sortData;

},


filter() {
var that = this;
that.glassshow = true;
that.isfilter = true;
that.zindex = 15;
},

reset() {
this.lowprice = "";
this.highprice = "";
for (var i = 0; i < this.sortlist.length; i++) {
this.sortlist[i].isactive = false;
}
},

makesure(){
that.isfilter = false;
that.glassshow = false;
},


getlist(page) {
var _this = this;
_this.havedata = false;
_this.$http.get(API.searchlist, {
params: {keywords:_this.keywords,sort:_this.sort,order:_this.order, page:page, limit:10}
}).then(res => {
var data = res.data.data;
if(data!=""){
_this.havedata = true;
}
if(page == 1){
_this.goodslist = [];
}
data.forEach(function(val,i){
_this.goodslist.push(val)
})
});
},
onRefresh(){




},

onInfinite(){

if(this.havedata){
this.getlist(this.page);
this.page++;
}

}
},
mounted() {
var _this = this;
_this.keywords = _this.$route.params.keywords
this.getlist(1);
this.getsort();

}
};



