
import XButton from 'vux/src/components/x-button/index.vue'

import Header from "../../../components/header/Header.vue";
import dialog_bottom from "../../../components/dialog_bottom/dialog_bottom.vue";
import { Dialog } from "vant";
import API from "../../../api/api.js";
import { setTimeout } from "timers";

export default {
components: { "header-view": Header, "dialog-bottom": dialog_bottom },

data() {
return {
userinfo: { type: 1, alipay_account: "", bank_name: "", bank_number: "" },
dialog_type: false,
type: [{ name: "银行卡", id: 2 }, { name: "支付宝", id: 1 }]
};
},
created() {
var _this = this;
_this.cardinfo();
},
methods: {
cardinfo() {
var _this = this;
_this.$http.get(API.cardInfo + this.$route.query.id, {}).then(res => {
if (res.data.code == 200) {
this.userinfo = res.data.data.info;
} else {
_this.$vux.toast.text(res.data.msg, "middle");
}
});
},
ckeckcard() {
var _this = this;
_this.$http
.post(API.cardCheck, {
bank_number: _this.userinfo.bank_number
})
.then(res => {
if (res.data.code == 200) {
this.userinfo.bank_name = res.data.data.bankName;
} else {
_this.$vux.toast.text(res.data.msg, "middle");
}
});
},
cardsave() {
var _this = this;
_this.$http
.put(API.cardUpdate + this.$route.query.id, this.userinfo)
.then(res => {
if (res.data.code == 200) {
_this.$vux.toast.text(res.data.msg, "middle");
setTimeout(function() {
_this.$router.push({
name: "mycard",
query: { choosecard: _this.$route.query.choosecard ? 1 : 0 }
});
}, 1500);
}
});
},
hide_dialog_type() {
this.dialog_type = !this.dialog_type;
},
checked_type(i) {
this.userinfo.type = this.type[i].id;
}
},
computed: {
mytype: function() {
var type = this.userinfo.type;
if (type == 2) {
type = "银行卡";
} else if (type == 1) {
type = "支付宝";
} else {
type = "未设置";
}
return type;
}
}
};



